import PropTypes from 'prop-types';

const FeedbackOptions = ({ options, handleFeedback }) => {
    return (

    <div>
      {options.map((el) => (
          <button 
          key={el}
          id={el}
          type='button'
          onClick={() => handleFeedback(el)}>{el.charAt(0).toUpperCase() + el.slice(1)}
        </button>
      ))}
  </div>
)
}


export default FeedbackOptions;